img {
  -o-object-fit: cover;
     object-fit: cover;
  font-family: 'object-fit: cover';
}

.img-wrap:before {
  -webkit-animation: img-wrap 2s cubic-bezier(.4, 0, .2, 1) forwards;
          animation: img-wrap 2s cubic-bezier(.4, 0, .2, 1) forwards;
  background: #fff;
  bottom: 0;
  content: '';
  left: 0;
  pointer-events: none;
  position: absolute;
  right: 0;
  top: 0;
  z-index: 1;
}

@-webkit-keyframes img-wrap {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

@keyframes img-wrap {
  100% {
    -webkit-transform: translateX(100%);
            transform: translateX(100%);
  }
}

