/* start */

/*ボタン*/

.hamburger-menu {
 display: none;
}

.hamburger_btn {
  position: fixed; /*常に最上部に表示したいので固定*/
  top: 0;
  right: -300;
  width: 70px;
  height: 72px;
  cursor: pointer;
  z-index: 50;
}

.hamburger_btn .line {
  position: absolute;
  top: 0;
  left: 20px;
  width: 32px;
  height: 2px;
  background: #333333;
  text-align: center;
}

.hamburger_btn .line_01 {
  top: 16px;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
}

.hamburger_btn .line_02 {
  top: 26px;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
}

.hamburger_btn .line_03 {
  top: 36px;
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
}

.btn_line01 {
  -webkit-transform: translateY(10px) rotate(-45deg);
          transform: translateY(10px) rotate(-45deg);
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
}

.btn_line02 {
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
  opacity: 0;
}

.btn_line03 {
  -webkit-transform: translateY(-10px) rotate(45deg);
          transform: translateY(-10px) rotate(45deg);
  -webkit-transition: 0.4s ease;
  transition: 0.4s ease;
}

/*サイドバー*/

.menu-enter-active, .menu-leave-active {
  -webkit-transition: opacity 0.4s;
  transition: opacity 0.4s;
}

.menu-enter, .menu-leave-to {
  opacity: 0;
}

.menu-leave, .menu-enter-to{
  opacity: 1;
}

.menu li {
  list-style: none;
  line-height: 1;
  padding: 1rem;
}

.menu {
  background-color: white;
  z-index: 30;
  padding: 2rem 1rem;
  position: fixed;
  width: 100%;
  height: 300px;
  top: 65px;
  right: 0;
}

.menu a {
  color: rgb(66, 66, 66);
  text-decoration: none;
  font-size: 1.2rem;
  padding: 0 2rem;
  margin-top: 5px;
}

.menu a :hover {
  background-color: rgba(135, 206, 235, 0.9);
  opacity:0.8;
}

.menu ul{
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  margin: 1rem;
  padding: 0;
}

